import React, { useState, useEffect } from "react";
import { Layout, Menu, Image } from "antd";
import { LaptopOutlined } from "@ant-design/icons";
import Logo from "../assets/icon.png";
import { Link } from "react-router-dom";
import axios from "axios";
const { SubMenu } = Menu;

export const Sider = () => {
  const [categories, setCategories] = useState(null);

  useEffect(() => {
    (async function loadData() {
      const response = await axios.get(
        `https://raw.githubusercontent.com/quankori/book-english/master/database/data.json`
      );
      setCategories(response.data);
    })();
  });

  return (
    <Layout.Sider breakpoint="md" collapsedWidth="0" className="layout-sider">
      <div
        className="logo"
        style={{
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Image width={50} src={Logo} />
      </div>
      <Menu theme="dark" mode="inline">
        {categories &&
          categories.map((x, key) => {
            return (
              <SubMenu icon={<LaptopOutlined />} title={x.title} key={key}>
                {x.blogs.map((blog) => {
                  const url = `/blog?path=${blog.path}`;
                  return (
                    <Menu.Item key={blog.title}>
                      <Link to={url}>{blog.title}</Link>
                    </Menu.Item>
                  );
                })}
              </SubMenu>
            );
          })}
      </Menu>
    </Layout.Sider>
  );
};
