import React from "react";
import { Layout, Menu } from "antd";
import { Link } from "react-router-dom";

export const Header = () => {
  return (
    <Layout.Header className="site-header">
      <Menu mode="horizontal" defaultSelectedKeys={["1"]}>
        <Menu.Item key="1">
          <Link to="/">Home</Link>
        </Menu.Item>
      </Menu>
    </Layout.Header>
  );
};
