import ReactMarkdown from "react-markdown";
import gfm from "remark-gfm";
import React, { useState, useEffect } from "react";
import { vscDarkPlus as theme } from "react-syntax-highlighter/dist/esm/styles/prism";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { useLocation } from "react-router-dom";
import axios from "axios";
export const Blog = () => {
  const useQuery = () => {
    return new URLSearchParams(useLocation().search);
  };
  const query = useQuery();
  const [blog, setBlog] = useState(null);
  const path = query.get("path");
  const renderers = {
    code: ({ language, value }) => {
      return (
        <SyntaxHighlighter style={theme} language={language} children={value} />
      );
    },
  };

  useEffect(() => {
    (async function loadData() {
      const response = await axios.get(
        `https://raw.githubusercontent.com/quankori/book-english/master/${path}`
      );
      const text = await response.data;
      setBlog(text);
    })();
  });

  return (
    <div className="App" style={{ minHeight: "85vh" }}>
      <ReactMarkdown plugins={[gfm]} renderers={renderers} children={blog} />
    </div>
  );
};
